.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  height: 110%;

  background-color: rgb(133, 133, 133);
  color: white;
  border: none;

  font-size: 20px;
  transition: 0.5s ;

  box-shadow: 2px 2px rgba(24, 24, 24, 0.25);
}

.btn:hover {
  background-color: rgb(172, 172, 172);
  box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  color: white;
}

.inp {
  margin-bottom: 5%;
  border: none;
  border-radius: 15px;

  width: 60%;
  height: 20px;

  padding: 10px;

  font-size: 16px;

  background-color: rgb(228, 228, 228);
}

.inp:focus {
  outline: none;
}