.main {
  height: 100vh;
}

.nav {
  padding: 15px 20px;

  height: 5vh;
  background-color: #ededed;

  display: flex;
  justify-content: space-between;
}

.btn {
  height: 110%;

  background-color: #dadada;
  color: white;
  border: none;

  font-size: 20px;
  transition: 0.5s ;

  box-shadow: 2px 2px rgba(24, 24, 24, 0.25);
}

.btn:hover {
  background-color: rgb(172, 172, 172);
  box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  color: white;
}

.grid {
  height: 91vh;

  display: grid;
  grid-template-columns: 1fr 4fr;

  overflow: hidden;
}

