.inp {
  width: 70%;
  height: 35%;
  padding: 15px;

  border-radius: 25px;

  border: none;

  font-size: 22px;
}

.inp:focus {
  outline: none;
}

.btn {
  height: 50%;

  background-color: #dadada;
  color: white;
  border: none;

  font-size: 20px;
  transition: 0.5s ;

  box-shadow: 2px 2px rgba(24, 24, 24, 0.25);
}

.btn:hover {
  background-color: rgb(172, 172, 172);
  box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  color: white;
}