.cont {
  width: 80%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.inp {
  border: none;
  border-radius: 15px;

  width: 60%;
  height: 20px;

  padding: 10px;

  font-size: 16px;
}

.inp:focus {
  outline: none;
}

.btn {
  height: 110%;

  background-color: #dadada;
  color: white;
  border: none;

  font-size: 20px;
  transition: 0.5s ;

  box-shadow: 2px 2px rgba(24, 24, 24, 0.25);
}

.btn:hover {
  background-color: rgb(172, 172, 172);
  box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  color: white;
}