.container {
  width: 90%;
  height: 10vh;
  background-color: rgb(228, 228, 228);

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 10%;

  border-bottom: 4px solid rgb(189, 189, 189);
  transition: 0.5s;

  margin-bottom: 20px;
  margin-top: 20px;
}

.title {
  font-weight: 300;
  font-size: 20px;
  margin: 0;
}

.container:hover {
  background-color: rgb(211, 211, 211);
}

.active {
  height: 20vh;

  background-color: rgb(189, 189, 189);
  border-color: rgb(165, 165, 165);
}

.active:hover {
  background-color: rgb(148, 148, 148);
}

.copyBtn {
  margin-top: 10px;
  cursor: pointer;
}

.copyBtn:hover {
  color: rgb(236, 236, 236);
}

.copyBtn:active {
  color: rgb(207, 207, 207);
}

.overflowToggle {
  height: 100%;
  overflow-y: scroll;
}

.toggleModal {
  cursor: pointer;
}

.toggleModal:hover {
  color: rgb(236, 236, 236);
}

.toggleModal:active {
  color: rgb(207, 207, 207);
}