.message {
  display: flex;
  align-items: center;
  margin: 0;

  border-radius: 7vh;

  width: fit-content;

  padding-right: 10%;
  margin-bottom: 10px;
}

.sent {
  align-self: flex-start;
  background-color: white;
}

.sent > p {
  color: #8f8f8f;
}

.recieved {
  align-self: flex-end;
  background-color: #dcf8c6;
  color: #41493B;
}

.recieved > p {
  color: #646c5d;
}

.image {
  border-radius: 50%;
  height: 9vh;
  margin-right: 10px;
}